.feedback {
    width: 100%;
    height: 100%;
    padding: 0px 0px 100px 0px;
    background-color: #00000000;

    @media(max-width:768px) {
        padding: 0px 0px 50px 0px;
    }

    &__title {
        text-align: center;
        font-size: 2.25rem;
        font-weight: 700;
        margin-bottom: 30px;
        text-transform: uppercase;

        @media (max-width: 576px) {
            font-size: 1.875rem;
            margin-bottom: 20px;
        }

        @media (max-width: 400px) {
            font-size: 1.5rem;
        }   
    }

    &-wrapper {
        position: relative;
        display: flex;
        align-items: flex-start;
        transition: right 0.3s ease-in-out;
        // margin-right: 20px;
        margin-bottom: 40px;
        // padding: 0px 30px;
    }
    &-item {
        display: flex;
        flex-direction: row;
        // margin-right: 20px;
        max-width: 100%;
        flex: 0 0 100%;
        @media(max-width:768px) {
            flex: 0 0 100%;
            max-width: 100%;
        }

        &-block {
            border: 1px solid rgb(180, 178, 178);
            border-radius: 8px;
            padding: 20px;
            transition: 0.3s ease;
            margin-right: 20px;

            &:hover {
                background-color: #488ebd;
            }

            @media(max-width:768px) {
                padding: 10px;
            }

            &__user {
                display: flex;
                flex-direction: column-reverse;
                align-items: center;
                & img {
                    width: 40px;
                    height: 40px;
                    margin-bottom: 5px;
                }
            }
    
            &__rating {
                text-align: center;
                margin-bottom: 20px;
            }
    
            &__text {
                transition: 0.3s ease;
                overflow: hidden;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                max-width: 350px;
                text-align: center;
            }

        }

        &:hover &__text{
            overflow: visible;
            -webkit-line-clamp: unset;
        }
        
        // &:nth-of-type(2n) {
        //     border: 4px solid red;
        //     background-color: aqua;
        // }
    }
    &-arrows {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 15px;

        &__item {
            width: 220px;
            padding: 8px 20px;
            align-items: center;
            justify-content: center;
            height: 100%;
            display: inline-block;
            line-height: 41px;
            background-color: #3e9cdc;
            padding: 0 15px;
            font-size: .8125rem;
            text-transform: uppercase;
            color: #fff;
            text-align: center;
            cursor: pointer;
            border: 1px solid #fff;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            -webkit-transition: transform .3s, box-shadow .3s;
            transition: transform .3s, box-shadow .3s;
            border-radius: 4px;

            &:hover {
                -webkit-transform: translate3d(3px, -3px, -3px);
                transform: translate3d(3px, -3px, -3px);
                -webkit-box-shadow: -3px 3px 10px 0 #3e9cdc;
                box-shadow: -3px 3px 10px 0 #3e9cdc;
            }

            &_blocked {
                background-color: #ffffffa6;
                color: #2a2b30;
                border-color: #6f6d7b;
                cursor: not-allowed;
                &:hover {
                -webkit-transform: translate3d(0px, -0px, -0px);
                transform: translate3d(0px, -0px, -0px);
                -webkit-box-shadow: -0px 0px 0px 0 #3e9cdc;
                box-shadow: -0px 0px 0px 0 #3e9cdc;
                }
            }
        }
    }
}

.feedback-item:last-child .feedback-item-block:last-child {
    margin-right: 0
}