.main-screen {
  padding: 200px 0px;
  background-image: url(./../images/bg/main-banner.webp);
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  @media (max-width: 768px) {
    padding: 150px 0px 100px 0px;
  }
  &__wrapper {
    max-width: 780px;
  }
  &__title {
    text-transform: uppercase;
    font-size: rem(48);
    font-weight: 700;
    color: #000;
    margin-bottom: 80px;
    @media (max-width: 1024px) {
      font-size: rem(42);
    }
    @media (max-width: 576px) {
      font-size: rem(38);
    }
    @media (max-width: 400px) {
      font-size: rem(24);
      margin-bottom: 40px;
    }
  }
  &__btns {
    line-height: 0;
  }
  &__btn {
    max-width: 183px;
    margin-right: 30px;
    @media (max-width: 450px) {
      display: block;
      margin-right: 0;
      margin-bottom: 15px;
    }
    &:last-child {
      margin-right: 0;
      @media (max-width: 450px) {
        margin-bottom: 0px;
      }
    }
  }
}

.main-screen-list {
  color: #000;
  margin-bottom: 80px;
  @media (max-width: 1024px) {
    margin-bottom: 40px;
  }
  &__title {
    font-size: rem(20);
    font-weight: 700;
    margin-bottom: 10px;
    @media (max-width: 576px) {
      font-size: rem(16);
    }
  }
  &__regul {
    padding-left: 25px;
  }
  &__item {
    position: relative;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: rem(20);
    @media (max-width: 576px) {
      font-size: rem(16);
    }
  }
  &__item::before {
    content: "";
    display: inline-block;
    position: absolute;
    left: -20px;
    top: 10px;
    width: 5px;
    height: 1.5px;
    background-color: #000;
  }
}
