.footer {
  background-color: #3d4050;
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    height: 80px;
    @media (max-width: 1024px) {
      height: auto;
      padding: 20px 0px;
      gap: 15px;
    }
  }
  &-copyrigth {
    font-size: rem(13);
    flex: 0 0 30%;
    max-width: 30%;
    &__text {
      max-width: 290px;
      @media (max-width: 1024px) {
        max-width: 100%;
      }
    }
    @media (max-width: 1024px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: 2;
    }
  }
  &-payment {
    flex: 0 0 40%;
    max-width: 40%;
    display: flex;
    justify-content: space-between;
    padding: 0px 35px;
    border-left: 1px solid $silver;
    gap: 15px;
    &:last-child {
      padding-right: 0px;
    }
    @media (max-width: 1170px) {
      padding: 0 18px;
    }
    @media (max-width: 1024px) {
      justify-content: start;
      border-left: 0px;
      padding: 0px;
    }
    @media (max-width: 400px) {
      flex-direction: column;
      justify-content: flex-start;
    }
    @media (max-width: 1024px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: 1;
    }
  }
  &-links {
    flex: 0 0 30%;
    max-width: 30%;
    display: flex;
    justify-content: space-between;
    padding: 0px 35px;
    border-left: 1px solid $silver;
    gap: 15px;
    &:last-child {
      padding-right: 0px;
    }
    @media (max-width: 1170px) {
      padding: 0 18px;
    }
    @media (max-width: 1024px) {
      justify-content: start;
      border-left: 0px;
      padding: 0px;
    }
    @media (max-width: 400px) {
      flex-direction: column;
      justify-content: flex-start;
    }
    @media (max-width: 1024px) {
      flex: 0 0 100%;
      max-width: 100%;
      order: 1;
    }
  }
}

.footer-payment-item {
  display: inline-block;
  white-space: nowrap;
  &:first-child {
    margin-left: 0;
  }
  &__icon,
  &__title {
    display: inline-block;
    vertical-align: middle;
  }
  &__title {
    color: $silver;
    font-size: rem(13);
  }
  &__icon {
    margin-right: 10px;
    @media (max-width: 1170px) {
      margin-right: 8px;
    }
  }
}

.footer-links-item {
  display: inline-block;
  white-space: nowrap;
  &:hover {
    .footer-links__item-icon {
      fill: #fff;
    }
  }
  &:first-child {
    margin-left: 0;
  }
  &__icon,
  &__title {
    display: inline-block;
    vertical-align: middle;
  }
  &__title {
    color: $silver;
    font-size: rem(13);
    color: #fff;
    transition: 0.3s ease;
    &:hover {
      color: #3e9cdc;
    }
  }
  &__icon {
    margin-right: 10px;
    height: 20px;
    width: 20px;
    fill: #3e9cdc;
    transition: fill 0.2s ease-in-out;
    @media (max-width: 1170px) {
      margin-right: 8px;
    }
  }
}
